import { FC, Suspense, lazy } from 'react';

import { ThemeProvider } from 'styled-components';
import { HelmetProvider } from 'react-helmet-async';

import { HomePage } from './pages/homePage';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import {
  mobileTheme,
  webTheme,
  Layout,
} from './components/shared/layout/index';
const ProjectsPage = lazy(() => import('./pages/projectsPage'));
const Project = lazy(() => import('./features/projects/project'));
const ProjectsIndex = lazy(() => import('./features/projects/index'));
const BlogsPage = lazy(() => import('./pages/blogsPage'));
const Blog = lazy(() => import('./features/blogs/blog'));
const BlogsIndex = lazy(() => import('./features/blogs/index'));
const AboutPage = lazy(() => import('./pages/aboutPage'));
const AboutIndex = lazy(() => import('./features/about/index'));
const Experience = lazy(() => import('./features/about/experience'));
const Education = lazy(() => import('./features/about/education'));
const ServicesPage = lazy(() => import('./pages/servicesPage'));
const CertificationsPage = lazy(() => import('./pages/certificationsPage'));
const ContactPage = lazy(() => import('./pages/contactPage'));
import NoMatchPage from './pages/noMatchPage';
import { useWindowWidth } from './hooks/windowWidth';
import { designThreshold } from './constants/designThreshold';
//import Loading from './pages/loading';

const App: FC = () => {
  const size = useWindowWidth();
  return (
    <>
      <ThemeProvider theme={size <= designThreshold ? mobileTheme : webTheme}>
        <HelmetProvider>
          <Router>
            <Suspense fallback={<></>}>
              <Routes>
                <Route path='/' element={<Layout />}>
                  <Route index element={<HomePage />} />
                  <Route path='projects' element={<ProjectsPage />}>
                    <Route index element={<ProjectsIndex />} />
                    <Route path=':projectId' element={<Project />} />
                  </Route>
                  <Route path='about' element={<AboutPage />}>
                    <Route index element={<AboutIndex />} />
                    <Route path='experience' element={<Experience />} />
                    <Route path='education' element={<Education />} />
                  </Route>
                  <Route path='blogs' element={<BlogsPage />}>
                    <Route index element={<BlogsIndex />} />
                    <Route path=':blogId' element={<Blog />} />
                  </Route>
                  <Route path='services' element={<ServicesPage />} />
                  <Route
                    path='certifications'
                    element={<CertificationsPage />}
                  />
                  <Route path='contact' element={<ContactPage />} />
                  <Route path='*' element={<NoMatchPage />} />
                </Route>
              </Routes>
            </Suspense>
          </Router>
        </HelmetProvider>
      </ThemeProvider>
    </>
  );
};

export default App;
